  box-shadow: 0 0 0 2px #000;
}

.checkbox:checked + .checkbox__icon {
  fill: #fff;
  background-color: #d41443;
  border-color: #d41443;
}

.checkbox:not(:checked):not(:disabled):not(:focus) + .checkbox__icon:hover {
  border-color: #d41443;
}

.checkbox:disabled + .checkbox__icon {
  opacity: .5;
  border-color: #000;
}

.checkbox:disabled:checked + .checkbox__icon {
  background-color: #000;
}

.checkbox:not(:disabled) + .checkbox__icon {
  cursor: pointer;
}

.checkbox__use {
  transform-origin: center;
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.2)rotate(-45deg);
}

.checkbox:checked + .checkbox__icon .checkbox__use {
  transform: scale(1.2);
