.btn {
  @include centeredFlex(inline-flex);

  line-height: $line-height;
  text-decoration: none;
  color: $color-text-lighter;

  fill: $color-text-lighter;
  background-color: $color-accent;
  border: 1px solid $color-accent;
  border-radius: 50px;

  transition: $transition-animation $transition-duration;
  transition-property: background-color, border-color, color, fill;

  &:hover,
  &:focus {
    background-color: $color-accent-dark;
    border-color: $color-accent-dark;
  }

  &:not(.btn--circle) {
    min-width: 165px;
    padding: 7px 17px;

    @include mq(tablet) {
      padding-left: 35px;
      padding-right: 35px;
    }
  }
}

// btn content inner space
.btn__text + .btn__icon,
.btn__icon + .btn__text {
  margin-left: 12px;
}

.btn__icon {
  transform: translateY(1px);
}

// btn variants
.btn--100 {
  width: 100%;
}

.btn--sm {
  padding: 9px 14px;
  line-height: 1.3;
}

.btn--secondary {
  color: $color-accent;
  fill: $color-accent;
  background-color: $bg-button-secondary;
  border-color: $bg-button-secondary;

  &:hover,
  &:focus {
    background-color: $bg-button-secondary-active;
    border-color: $bg-button-secondary-active;
  }
}

.btn--inverse {
  color: $color-accent;
  fill: $color-accent;
  background-color: $bg-button-secondary;
  border-color: $bg-button-secondary;

  &:hover,
  &:focus {
    background-color: $color-accent;
    border-color: $color-accent;
    fill: $color-text-lighter;
  }
}

.btn--bold {
  font-weight: 700;
}

.btn--circle {
  display: flex;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
